import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import "./styles.css";
import "bootstrap/dist/css/bootstrap.css";
import { Link } from 'react-router-dom';

function NavBar(props) {
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Navbar.Brand >{props.heading}</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ms-auto">
            <Nav.Link as={Link} to="/my-portfolio" className="px-3 m-auto">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to="/my-portfolio/projects" className="px-3 m-auto">
              Projects
            </Nav.Link>
            <Nav.Link as={Link} to="/my-portfolio/experience" className="px-3 m-auto">
              Experience
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBar;
